import React, { Component } from 'react';
import styled from 'styled-components';
import { backgrounds, lqBackgrounds } from './media';
import alphaColorBg from '../assets/30_percent_opacity_black.png';

const BackgroundStyled = styled.div`
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  background-image: url(${alphaColorBg});
  background-size: cover;
  background-attachment: fixed;

  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  > img:nth-child(1) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: blur(40px);
    filter: scale(30%);
    z-index: 0;
  }
  > img:nth-child(2) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
  }
  > img:nth-child(3) {
    position: absolute;
    // top: 20vh;
    left: 0;
    width: 100%;
    object-fit: contain;
    z-index: 2;
  }
}
`;

class Background extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
      loaded1: false,
      loaded2: false,
    }
  }

  showFirstLayer = () => {
    this.setState({
      loaded1: true,
    })
  }

  showSecondLayer = () => {
    this.setState({
      loaded2: true,
    })
  }
  
  render() {
    const { icon } = this.props;
    const { loaded1, loaded2 } = this.state; 
    return (
      <BackgroundStyled>
        {/* lq image */}
        <img src={lqBackgrounds[`bg${icon}1_lq`]} alt='lq bg'/>
        {/* background */}
        <img 
        style={loaded1? {display: 'inline'} : {display: 'none'}} 
        onLoad={() => this.showFirstLayer()} 
        class={`${loaded1? 'animate__animated animate__zoomIn' : null}`} 
        src={backgrounds[`bg${icon}1`]} 
        alt=""
        />
        {/* animated layer */}
        <img 
        style={(loaded1 && loaded2) ? {display: 'inline'} : {display: 'none'}} 
        onLoad={() => this.showSecondLayer()} 
        class={`${(loaded1 && loaded2)? 'animate__animated animate__fadeInDown' : null}`} 
        src={backgrounds[`bg${icon}2`]} 
        alt=""
        />
      </BackgroundStyled>
    )
  }
}

export default Background
